import React, {Component} from "react";
import FuzzySearch from "fuzzy-search";

import ReactTable from "react-table";
import {
    Button,
    ButtonGroup,
    Card,
    CardBody,
    CardHeader,
    Col,
    Container,
    FormInput,
    FormSelect,
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    Row
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import RangeDatePicker from "../components/common/RangeDatePicker";

class Tables extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            name: '',
            pageSizeOptions: [5, 10, 15, 20, 25, 30],
            pageSize: 10,
            tableData: [],
            addressColumn: '',
        };

        this.searcher = null;

        this.getStatusClass = this.getStatusClass.bind(this);
        this.handlePageSizeChange = this.handlePageSizeChange.bind(this);
        this.handleFilterSearch = this.handleFilterSearch.bind(this);
        this.handleItemEdit = this.handleItemEdit.bind(this);
        this.handleItemDelete = this.handleItemDelete.bind(this);
        this.handleItemConfirm = this.handleItemConfirm.bind(this);
        this.handleItemViewDetails = this.handleItemViewDetails.bind(this);
    }

    componentWillMount() {
        // const tableData = getTransactionHistoryData();
        this.getData();
        // Initialize the fuzzy searcher.
        this.searcher = new FuzzySearch(this.state.tableData, ["customer", "status"], {
            caseSensitive: false
        });
    }

    /**
     * Returns the appropriate status class for the `Status` column.
     */
    getStatusClass(status) {
        const statusMap = {
            Cancelled: "danger",
            Complete: "success",
            Pending: "warning"
        };

        return `text-${statusMap[status]}`;
    }

    /**
     * Handles the page size change event.
     */
    handlePageSizeChange(e) {
        this.setState({
            ...this.state,
            pageSize: e.target.value
        });
    }

    /**
     * Handles the global search.
     */
    handleFilterSearch(e) {
        this.setState({
            ...this.state,
            tableData: this.searcher.search(e.target.value)
        });
    }

    /**
     * Mock method for editing transactions.
     */
    handleItemEdit(row) {
        alert(`Editing transaction "${row.original.id}"!`);
    }

    /**
     * Mock method for deleting transactions.
     */
    handleItemDelete(row) {
        alert(`Deleting transaction "${row.original.id}"!`);
    }

    /**
     * Mock method for confirming transactions.
     */
    handleItemConfirm(row) {
        alert(`Confirming transaction "${row.original.id}"!`);
    }

    /**
     * Mock method for confirming transactions.
     */
    handleItemViewDetails(row) {
        alert(`Viewing details for "${row.original.id}"!`);
    }

    getData() {
        //修改请求头
        let myHeaders = new Headers({
            'Access-Control-Allow-Origin': '*',
            'Content-Type': 'text/plain'
        });
        fetch('/customers/list', {
            method: 'GET',
            headers: myHeaders,
            mode: 'cors',
            //转或称字符串格式
        }).then(res => res.json()).then(
            data => {
                this.getAddress(data);
                // console.log(data);
            }
        )
    }
    getAddress(data) {
        var index;
        var adr = '';
        const address = data;
        var entireAddress;
        for (index in data) {
            if (address[index].address === null) {
                adr = address[index].address === null ? "" : (address[index].address + ", ");
            }
            if (address[index].cityDistricts != null) {
                entireAddress = adr + address[index].cityDistricts.zipCode + ", " + address[index].cityDistricts.district + ", " + address[index].cityDistricts.city;
            }
            delete address[index].cityDistricts;
            address[index].address = entireAddress;
            this.setState({
                tableData: address,
            });
        }
    }


    render() {
        const {tableData, pageSize, pageSizeOptions} = this.state;
        const tableColumns = [
            // {
            //     Header: "序列号",
            //     accessor: "id",
            //     maxWidth: 200,
            //     className: "text-right"
            // },
            {
                Header: "姓名",
                accessor: "name",
                maxWidth: 100,
                className: "text-center"
            },
            {
                Header: "性别",
                accessor: "gender",
                maxWidth: 60,
                className: "text-center"
            },
            // {
            //     Header: "Birthday",
            //     accessor: "birthday",
            //     className: "text-center",
            //     minWidth: 200,
            //     Cell: row =>
            //         dateFormat(new Date(row.original.date), "dddd, mmmm dS, yyyy")
            // },
            {
                Header: "邮箱",
                accessor: "email",
                maxWidth: 200,
                // Cell: row => (
                //     <span className={this.getStatusClass(row.original.status)}>
                //         {row.original.status}
                //     </span>
                // ),
                className: "text-center"
            },
            {
                Header: "联系方式",
                accessor: "phoneNumber",
                maxWidth: 150,
                // Cell: row => <span className="text-success">{row.original.total}</span>,
                // className: "text-center"
            },
            {
                Header: "注册时间",
                accessor: "startDate",
                className: "text-center",
                minWidth: 120,
                // Cell: row =>
                //     dateFormat(new Date(row.original.date), "dddd, mmmm dS, yyyy")
            },
            {
                Header: "到期时间",
                accessor: "endDate",
                className: "text-center",
                minWidth: 120,
                // Cell: row =>
                //     dateFormat(new Date(row.original.date), "dddd, mmmm dS, yyyy")
            },
            {
                Header: "联系地址",
                accessor: "address",
                className: "text-center",
                minWidth: 200
            },
            {
                Header: "最近更新",
                accessor: "lastUpdatedDate",
                className: "text-center",
                minWidth: 120
            },
            {
                Header: "操作",
                accessor: "actions",
                maxWidth: 300,
                minWidth: 180,
                sortable: false,
                Cell: row => (
                    <ButtonGroup size="sm" className="d-table mx-auto">
                        <Button theme="white" onClick={() => this.handleItemConfirm(row)}>
                            <i className="material-icons">&#xE5CA;</i>
                        </Button>
                        <Button
                            theme="white"
                            onClick={() => this.handleItemViewDetails(row)}
                        >
                            <i className="material-icons">&#xE870;</i>
                        </Button>
                        <Button theme="white" onClick={() => this.handleItemEdit(row)}>
                            <i className="material-icons">&#xE254;</i>
                        </Button>
                        <Button theme="white" onClick={() => this.handleItemDelete(row)}>
                            <i className="material-icons">&#xE872;</i>
                        </Button>
                    </ButtonGroup>
                )
            }
        ];
        return (
            <Container fluid className="main-content-container px-4 pb-4">
                <Row noGutters className="page-header py-4">
                    <PageTitle title="Active Users" subtitle="Dashboards" className="text-sm-left mb-3"/>
                    <Col sm="4" className="d-flex ml-auto my-auto">
                        <RangeDatePicker className="justify-content-end"/>
                    </Col>
                </Row>
                <Card className="p-0">
                    <CardHeader className="p-0">
                        <Container fluid className="file-manager__filters border-bottom">
                            <Row>
                                {/* Filters :: Page Size */}
                                <Col className="file-manager__filters__rows d-flex" md="6">
                                    <span>Show</span>
                                    <FormSelect
                                        size="sm"
                                        value={this.state.pageSize}
                                        onChange={this.handlePageSizeChange}
                                    >
                                        {pageSizeOptions.map((size, idx) => (
                                            <option key={idx} value={size}>
                                                {size} rows
                                            </option>
                                        ))}
                                    </FormSelect>
                                </Col>

                                {/* Filters :: Search */}
                                <Col className="file-manager__filters__search d-flex" md="6">
                                    <InputGroup seamless size="sm" className="ml-auto">
                                        <InputGroupAddon type="prepend">
                                            <InputGroupText>
                                                <i className="material-icons">search</i>
                                            </InputGroupText>
                                        </InputGroupAddon>
                                        <FormInput onChange={this.handleFilterSearch}/>
                                    </InputGroup>
                                </Col>
                            </Row>
                        </Container>
                    </CardHeader>
                    <CardBody className="p-0">
                        <div className="">
                            <ReactTable
                                columns={tableColumns}
                                data={tableData}
                                pageSize={pageSize}
                                showPageSizeOptions={false}
                                resizable={true}
                            />
                        </div>
                    </CardBody>
                </Card>
            </Container>
        );
    }
}

export default Tables;
