/* eslint jsx-a11y/anchor-is-valid: 0 */
import {Link} from "react-router-dom";
import React, {Component} from "react";
import {
    Button,
    Card,
    CardBody,
    CardFooter,
    Col,
    Container,
    Form,
    FormCheckbox,
    FormGroup,
    FormInput,
    Row
} from "shards-react";
import axios from 'axios';


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            remember: false,
            auth: false,
            isMounted: false,
        };

    }


    componentDidMount() {
        this.setState({isMounted: true});
    }

    handleSubmit = (e) => {
        e.preventDefault();
        axios.post('/login', this.state)
            .then(res => {
                console.log(res.data);
                if (this.state.isMounted && res.data) {
                    this.setState({auth: res.data});
                    this.props.history.replace("/analytics");
                } else {
                    this.setState({
                        auth: res.data,
                    });
                    alert("用户名或密码不正确！请确认后重新登陆！");
                    this.props.history.replace("/login");
                }
            });
    };


    handleChange(e, params) {
        const newState = {};
        if (params === "remember") {
            newState[params] = !this.state[params];
        } else {
            newState[params] = e.target.value;
        }
        this.setState({...this.state, ...newState});
    }

    render() {
        return (
            <Container fluid className="main-content-container h-100 px-4">
                <Row noGutters className="h-100">
                    <Col lg="3" md="5" className="auth-form mx-auto my-auto">
                        <Card>
                            <CardBody>
                                {/* Logo */}
                                <img
                                    className="auth-form__logo d-table mx-auto mb-3"
                                    // src={require("../images/dashboard-logo.svg")}
                                    src={require("../images/avengers.svg")}
                                    alt="Dashboards - Login"
                                />
                                {/* Title */}
                                <h5 className="auth-form__title text-center mb-4">
                                    {/*Access Your Account*/}
                                </h5>
                                {/* Form Fields */}
                                <Form onSubmit={e => this.handleSubmit(e)}>
                                    <FormGroup>
                                        <label htmlFor="exampleInputEmail1">Email address</label>
                                        <FormInput type="email" id="inputEmail" name="username"
                                                   placeholder="Enter email"
                                                   autoComplete="email"
                                                   onChange={e => this.handleChange(e, "username")}/>
                                    </FormGroup>
                                    <FormGroup>
                                        <label htmlFor="exampleInputPassword1">Password</label>
                                        <FormInput type="password" id="inputPassword" name="password"
                                                   placeholder="Password"
                                                   autoComplete="current-password"
                                                   onChange={e => this.handleChange(e, "password")}/>
                                    </FormGroup>
                                    <FormGroup>
                                        <FormCheckbox id="remember" checked={this.state.remember}
                                                      onChange={e => this.handleChange(e, "remember")}>
                                            Remember me for 30 days.</FormCheckbox>
                                    </FormGroup>
                                    <Button pill theme="accent" className="d-table mx-auto" type="submit">
                                        Assemble
                                    </Button>
                                </Form>
                            </CardBody>
                            {/* Social Icons */}
                            <CardFooter>
                                <ul className="auth-form__social-icons d-table mx-auto">
                                    <li><a href="#"> <i className="fab fa-facebook-f"/> </a></li>
                                    <li><a href="#"> <i className="fab fa-twitter"/> </a></li>
                                    <li><a href="#"> <i className="fab fa-github"/> </a></li>
                                    <li><a href="#"> <i className="fab fa-google-plus-g"/> </a></li>
                                </ul>
                            </CardFooter>
                        </Card>

                        {/* Meta Details */}
                        <div className="auth-form__meta d-flex mt-4">
                            <Link to="/forgot-password">Forgot your password?</Link>
                            <Link to="/register" className="ml-auto">
                                Create a new account?
                            </Link>
                        </div>
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default Login;
